.messageBox{
    background: #f3f3f3;
    border-radius: 20px;
    padding: 5px 20px;
    color:white;
    display: inline-block;
    max-width: 80%;
}

.messageText{
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
}

.messageText img{
    vertical-align: middle;
}

.messageContainer{
    display: flex;
    justify-content: flex-end;
    padding: 0.5%;
    margin-top: 3px;
}

.sentText{
    display: flex;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    color: #828282;
    letter-spacing: 0.3px;

}

.pr-10{
    padding-right: 10px;
}
.justifyStart{
    justify-content: flex-start;
}
justifyEnd{
    justify-content: flex-end;
}
.colorWhite{
    color: white;
}
.colorDark{
    color:#353535;

}
.backgroundBlue{
    background: #2979ff;
}
.backgroundLight{
    background: #f3f3f3;
}